<template>
<div>
  <tou>哔哩哔哩 (゜-゜)つロ 干杯</tou>
   <swiper :swiperSlides="res"/>
   <div class="dak">
   <ul class="kuang" v-for="item in hotlist">
     <img :src="item.goodimg"/>
     <li>
       名称：{{item.goodname}}
     </li>
     <li>
       简介：{{item.info}}
     </li>
     <li>
       价格：{{item.price}}元
     </li>
   </ul>
   </div>
   <div class="clear"></div>
</div>
</template>

<script>
import swiper from "../base/swiper";
import {getSlider,getHot} from "../api";
import tou from "../base/tou";
  export default {
    name: 'home',
    data() {
      return {
       res:[],
       hotlist:[]
    }},

   created(){
    this.getBanner();
    this.getHot();
  },
  methods:{
   async getBanner(){
    let sliders=await getSlider();
    this.res=sliders;
    },
    async getHot(){
      let hotlist=await getHot();
      this.hotlist=hotlist;
    }
  },
  components:{
  swiper,
  tou
  }
  }
</script>
<style scoped>
*{
  list-style: none;
}
.kuang{
  float: left;
  width: 50%;
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.kuang li{
  font-size: 14px;
}
.kuang img{
  width: 220px;
  height: 220px;
}
 .dak{
   width: 98%;
   height: 100%;
   float: left;
   margin: 0 auto;
   padding-bottom: 58px;
 }
 .clear{
   clear: both;
 }
</style>



